  }
}

.section-padding5 {
  padding-top   : 2.5rem;
  padding-bottom: 2.5rem;

  @media (min-width: $sm) {
    padding-top   : 4rem;
    padding-bottom: 4rem;
  }

  @media (min-width: $md) {
