}

.section-padding9 {
  padding-top   : 3rem;
  padding-bottom: 3rem;

  @media (min-width: $sm) {
    padding-top   : 6rem;
    padding-bottom: 6rem;
  }

  @media (min-width: $md) {
    padding-top   : 9rem;
    padding-bottom: 9rem;
  }
}

.section-pd-tp10 {
  padding-top: 4rem;

  @media (min-width: $md) {
    padding-top: 10rem;
  }
}

.section-pd-tb10 {
  padding-top   : 4rem;
  padding-bottom: 4rem;

  @media (min-width: $md) {
    padding-top   : 10rem;
    padding-bottom: 10rem;
  }
}

.rec-pd13 {
  padding-top: 2rem !important;

  @media (min-width: $sm) {
    padding-top: 0rem !important;
  }

  @media (min-width: $md) {
    padding-top: 9rem !important;
  }
}

.rec-shadow {
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.09);
  border    : none !important;
}


