  }

  @media (min-width: 992px) {
    padding-top   : 100px;
    padding-bottom: 100px;
  }
}

.pb-4 {
  padding-bottom: 2rem !important;

  @media (min-width: 768px) {
    padding-bottom: 4rem !important;
  }
}

